.popover-cover {
    h4 {
        color: $fontColor;
        font-size: rem(12px);
        font-weight: 500;
        letter-spacing: 0.04em;
        line-height: rem(20px);
        margin: 8px 8px 7px 0;
    }

    .header-cover {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 8px;
        padding: 4px;
        margin: 0 -4px;
        height: 74px;
        border: 1px solid rgba(9, 30, 66, 0.08);

        .header-full-cover {
            background: #5e6c84;
            opacity: 0.3;
            padding-top: 37px;
        }

        .selected {
            box-shadow: 0 0 0 2px #ffffff, 0 0 0 4px #0079bf;
        }

        .paragraph {
            height: 31px;
            background: #5e6c84;
            opacity: 0.3;
            margin-bottom: 3px;
        }

        .row {
            height: 5px;
            width: 70%;
            background: #5e6c84;
            opacity: 0.3;
            border-radius: 3px;
            margin-bottom: 3px;
            margin-left: 2px;
        }

        .second {
            width: 50%;
        }

        .full {
            background: #ffffff;
            opacity: 1;
        }
    }
}

.colors-for-cover {
    margin-top: 2px;
}

.color-plate {
    display: grid;
    gap: 8px;
    padding: 3px;
    margin: 0 -4px;
    overflow-x: hidden;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    max-width: fit-content;

    .color-sqr {
        height: 32px;
        border-radius: 3px;
        width: 50px;

        &.color-selected {
            box-shadow: 0 0 0 2px #ffffff, 0 0 0 4px #0079bf;
            filter: none;
        }

        &:hover {
            filter: saturate(85%) brightness(85%);
        }
    }
}

.cover-img-plate {
    display: grid;
    grid-gap: 8px;
    grid-template-columns: 1fr 1fr 1fr;
    padding: 4px;
    margin: 0 -4px;

    .img-cover {
        border-radius: 3px;
        height: 67px;
        background-size: cover;
        height: 48px;
        width: 88px;
        background-repeat: no-repeat;
        background-position: center;

        &.cover-selected {
            box-shadow: 0 0 0 2px #ffffff, 0 0 0 4px #0079bf;
            filter: none;
        }
    }
}

.btn {
    border-radius: 3px;
    width: 100%;
    line-height: 20px;
    padding: 6px 12px;
    margin: 4px 0 0;
    background-color: #091e420a;
    text-align: center;

    &:hover {
        background-color: #091e4214;
    }
}

.file-input {
    display: none;
}
