.task-preview {
    align-items: center;
    .checkbox.edit {
        align-self: flex-start;
    }
}

// RESPONVSIVE

@media screen and (min-width: 784px) {
    .task-preview {
        .delete-task {
            display: none;
        }
        &:hover {
            .delete-task {
                display: block;
            }
        }
    }
}
